/* ------------------------------------------------------------------------------------------------ */
/* Responsive */
/* ------------------------------------------------------------------------------------------------ */

@include maxtablet {
  .block-dashboard {
    .row {
      [class*="col-"] {
        margin-bottom: 16px;
        &:last-child {
          margin-bottom: 0; } } } } }
@include mobile {
  .header-stick {
    .block-dashboard {
      .inner-header {
        top: 50px;
        h1 {
          margin-bottom: 10px; } } } }

  .block-dashboard {
    margin-top: 170px;
    padding-bottom: 30px;
    .row {
      [class*="col-"] {
        margin-bottom: 16px;
        &:last-child {
          margin-bottom: 0; } } }

    .link-seemore {
      a,
      span {
        font-size: 10px;
        line-height: 20px;
        &:after {
          width: 14px;
          height: 14px; } } }

    .inner-header {
      position: fixed;
      // top: 40px
      top: 70px;
      left: 0;
      z-index: 101;
      .row {
        [class*="col-"] {
          margin-bottom: 0; } }
      .col-lg-7 {
        flex: 0 0 calc(100% - 90px);
        max-width: calc(100% - 90px); }
      h1 {
        font-size: 24px;
        line-height: 29px;
        margin-top: -30px;
        margin-bottom: 20px;
        max-width: 100%;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden; }
      .search {
        .form-group {
          .form-control {
            height: 46px;
            padding: 12px 16px;
            padding-left: 45px;
            white-space: nowrap;
            text-overflow: ellipsis; }
          &:after {
            width: 18px;
            height: 18px;
            top: 12px;
            left: 16px; } } }
      .breadcrubms {
        display: none; } }

    .box-card {
      .icon {
        display: inline-block;
        margin-bottom: 0; }
      h3 {
        font-size: 14px;
        line-height: 18px;
        margin-left: 20px;
        margin-bottom: 0;
        display: inline-block;
        vertical-align: top;
        max-width: calc(100% - 70px); }
      h4 {
        margin-left: 20px;
        margin-bottom: 0;
        display: inline-block;
        vertical-align: top;
        max-width: calc(100% - 70px); }
      h4 + .contact,
      h4 + .text {
        margin-top: -16px; }
      &:not(.gradient-green) {
        .ext-link {
          display: none; } }
      .text,
      .contact {
        font-size: 10px;
        line-height: 14px;
        margin-left: 70px; }
      .text + .link-seemore {
        margin-left: 70px;
        margin-top: 20px; }
      .ext-link {
        top: 16px;
        right: 16px; } }
    .box-po {
      padding-right: 0;
      h3 {
        font-size: 14px;
        line-height: 17px; }
      .progress-bar {
        margin-bottom: 20px; }
      .box-text {
        p {
          font-size: 16px;
          line-height: 20px; } }
      .row {
        .col-lg-6 {
          flex: 0 0 50%;
          max-width: 50%; } } }

    .section {
      &__head {
        h2 {
          font-size: 24px;
          line-height: 29px; }
        p {
          font-size: 12px;
          line-height: 16px;
          color: #707070; } } }
    .pagination-wrap {
      margin-top: 0;
      justify-content: flex-start;
      u {
        font-size: 10px;
        line-height: 12px; }

      &.left {
        display: none; }
      .pagination {
        li {
          span,
          a {
            width: 24px;
            font-size: 10px;
            line-height: 24px; } }
        .before,
        .after {
          display: none; } } }

    .po-and-features {
      padding-bottom: 30px;
      .col-lg-2 {
        flex: 0 0 50%;
        max-width: 50%;
        margin-bottom: 0;
        .box-card {
          padding: 20px;
          .icon {
            margin-bottom: 13px; }
          h3,
          h4 {
            margin-left: 0;
            margin-bottom: 8px;
            max-width: 100%;
            width: 100%; }
          h4 + .contact,
          h4 + .text {
            margin-top: 0; }
          .text,
          .contact {
            margin-left: 0; } }
        & + .col-lg-4 {
          margin-top: 16px; } }
      .row {
        &:nth-child(2) {
          .col-lg-3 {
            & + .col-lg-3 {
              display: none; } }
          .col-lg-6 {
            display: none; } } } }
    .your-do {
      padding: 30px 0;
      &__head {
        margin-bottom: 20px;
        h3 {
          font-size: 12px;
          line-height: 16px;
          margin-bottom: 6px; }
        h2 {
          font-size: 24px;
          line-height: 29px; }
        .date-box {
          margin-top: 0;
          margin-bottom: 0;
          max-width: 170px;
          display: block;
          .arrow {
            flex: 0 0 30px;
            max-width: 30px;
            display: inline-block;
            width: 30px;
            height: 30px;
            position: relative;
            .ic-arrow {
              width: 10px;
              height: 9px;
              position: absolute;
              top: calc(50% - 4px);
              left: calc(50% - 5px); } }

          .box-text {
            display: inline-block;
            font-size: 9px;
            line-height: 11px;
            padding: 8.5px 10px;
            flex: 0 0 60px;
            max-width: calc(100% - 60px); } } }
      &__do-det {
        // padding: 36px 26px
        padding: 30px 20px;
        .ext-link {
          display: none; }
        .col-lg-3 {
          flex: 0 0 50%;
          max-width: 50%;
          margin-bottom: 0;
          padding-left: 4px;
          padding-right: 4px;
          .box-card {
            padding: 20px;
            .icon {
              margin-bottom: 13px;
              display: none; }
            h3,
            h4 {
              margin-left: 0;
              margin-bottom: 8px;
              max-width: 100%;
              width: 100%; }
            h4 + .contact,
            h4 + .text {
              margin-top: 0; }
            .text,
            .contact {
              margin-left: 0; } } }
        .det-do {
          &-pending,
          &-send,
          &-sent {
            .box-card.po-do-det {
              h3 {
                font-size: 14px;
                line-height: 17px; } } } } }

      &__payload {
        .box-tab {
          height: auto;
          h3 {
            font-size: 18px;
            line-height: 11px; }
          .text {
            p {
              font-size: 10px;
              line-height: 14px; } }
          .tabs {
            margin-bottom: 0;
            .nav {
              justify-content: space-between; }
            .nav-item {
              display: inline-block;
              flex: 0 0 calc(100% / 3);
              max-width: 50px;
              text-align: center;
              .icon {
                width: 28px;
                height: 28px;
                text-align: center;
                margin-right: 0;
                margin-bottom: 8px; }
              span {
                display: block;
                font-size: 12px;
                line-height: 1em; } } } }
        .payload-list-box {
          min-height: auto;
          padding: 30px;
          overflow: hidden;
          overflow-x: auto;
          .payload-list {
            width: calc(100% * 2 + 30px);
            column-count: 2;
            column-gap: 0;
            padding-right: 30px;
            // display: flex
            // flex-wrap: wrap
            // margin-left: -15px
            // margin-right: -15px
            .item {
              // flex: 0 0 50%
              // max-width: 50%
              // padding-left: 15px
              // padding-right: 15px
              &:nth-child(n+6) {
                margin-left: 30px; }
              .icon-box {
                width: 30px;
                flex: 0 0 30px;
                padding: 0;
                padding-left: 8px; }
              .icon {
                width: 30px;
                height: 30px; }
              .arrow {
                width: 50px;
                height: 40px;
                flex: 0 0 50px; }
              .do-numb {
                flex: 0 0 calc(100% - 80px);
                max-width: calc(100% - 80px);
                padding: 0 10px; } } } }
        .pagination-wrap {
          display: none; } }

      &.det-list {
        .box-card {
          .icon {
            display: none; } } }
      & + .po-and-features {
        .row {
          flex-wrap: nowrap;
          overflow-x: auto;
          padding-bottom: 10px; }
        .col-lg-6 {
          display: none; } } }
    .agros-service {
      padding: 30px 0;
      .service {
        &__slider {
          .slider-item {
            height: 140px;
            padding: 20px;
            h4 {
              font-size: 14px;
              line-height: 1.5em; }
            p {
              max-width: 180px;
              font-size: 8px;
              line-height: 1.5em; }
            .link {
              left: 20px;
              font-size: 10px;
              line-height: 1.5em; } } } } }

    .po-history {
      padding-bottom: 0;
      &__list {
        margin-bottom: 4px;
        ul {
          li {
            a {
              padding: 10px;
              height: 60px; }
            .icon-box {
              width: 40px;
              height: 40px;
              flex: 0 0 40px; }
            .box-det {
              display: none;
              &.mobile {
                display: block;
                flex: 0 0 calc(100% - 83px);
                max-width: calc(100% - 83px); } }
            .po-numb {
              flex: 0 0 100%;
              max-width: 100%;
              border-right: 0;
              padding: 0 8px;
              span {
                font-size: 14px;
                line-height: 1.5em;
                display: block;
                width: 100%;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis; } }
            .bottom {
              padding: 0 8px;
              font-size: 10px;
              line-height: 20px;
              display: block;
              width: 100%;
              overflow: hidden;
              white-space: nowrap;
              text-overflow: ellipsis;
              span {
                padding-right: 8px;
                border-right: 1px solid #cacaca;
                & + span {
                  padding-left: 8px; }
                &:last-child {
                  border-right: 0;
                  padding-right: 0; }

                u {
                  text-decoration: none;
                  &:first-child {
                    color: #FFBB00; }
                  &:last-child {
                    color: #4C9F70; } } } }

            .arrow {
              width: 53px;
              height: 60px; } } } } }

    .po-form {
      padding: 30px 0;
      .form-wrapper {
        padding: 30px; }
      & + .po-and-features {
        .row {
          .col-lg-3 {
            & + .col-lg-3 {
              display: none; } }
          .col-lg-6 {
            display: none; } } } } } }


@include responsive(400px) {
  .block-dashboard {
    .box-card {
      padding: 15px; }
    .po-and-features {
      & > .row {
        margin-left: -4px;
        margin-right: -4px; }
      .col-lg-2 {
        padding-right: 4px;
        padding-left: 4px; } } } }

@include responsive(350px) {
  .block-dashboard {
    .po-and-features {
      .col-lg-2 {
        flex: 0 0 100%;
        max-width: 100%;
        &:not(:last-child) {
          margin-bottom: 16px; } } }
    .box-card {
      h3 {
        margin-left: 10px;
        max-width: calc(100% - 60px);
        & + .text {
          margin-left: 60px; } }
      .contact {
        margin-left: 60px; }

      h4 {
        margin-left: 5px;
        max-width: calc(100% - 45px); }
      .text {
        margin-left: 55px;
        & + .link-seemore {
          margin-left: 55px; } } }
    .your-do__do-det {
      .col-lg-3 {
        flex: 0 0 100%;
        max-width: 100%;
        &:not(:last-child) {
          margin-bottom: 16px; }
        .box-card {} } } } }




